/* ==============================================================
   Variables
   ==============================================================*/
/* FONTS */
@fontSansSerif: "OpenSansRegular", "Helvetica Neue", Helvetica, Arial, sans-serif;
@fontSansSerifBold: "OpenSansSemibold", "Helvetica Neue", Helvetica, Arial, sans-serif;
@fontSerif: "MerriweatherRegular", "Times New Roman", Times, serif;
@fontSize: 0.875rem;
@baseMargin: 0 0 1.3125rem 0;
@images: "../../images";

/* BASIC */
@lightgray: #eee;
@orange: #f08421;

/* CI-COLORS */
@lowerblack: #222;
@placeholder: rgba(0,0,0,0.25);
@white: #fff;
@darkgray: #444;
@middlegray: #d5d5d5;
@neutralgray: #f2f3f4;
@blue: #3365a9;
@hoverblue: #507ebf;

/* MESSAGE COLORS */
@red: #c7254e;
@green: #468847;

/* BUTTON COLORS */
@btnDefault: #555;
@btnDefaultBorder: #ccc;
@btnDefaultBgc: #f7f7f7;
@btnDefaultHoverBgc: #f2f3f4;
@btnDanger: #721c24;
@btnDangerBorder: #721c24;
@btnDangerBgc: #f8d7da;
@btnDangerHoverBgc: #721c24;
@btnSuccess: #155724;
@btnSuccessBorder: #155724;
@btnSuccessBgc: #d4edda;
@btnSuccessHoverBgc: #155724;
@btnFullHoverBgc: #cce5ff;

/* STATISTICS */
@statBackgroundColor: #fffdf6;
@statDarkgray: #999;

/* NER */
@personColor: #0174DF;
@locationColor: #FF8000;
@corporationColor: #298A08;
@tagHoverColor: #eee;
@scaleHandleColor: #8A0808;

/* CROWDSOURCING */
@progressCompletion: #95C476;
@darkBlue: rgb(53,111,162);
@yellowTransparent: rgba(250, 250, 0, .3);
@blueTransparent: rgba(66,139,202,.3);

/* MEDIA QUERY BREAK POINTS */
@largeBreakpoint: 1200px;
@mediumBreakpoint: 992px;
@smallBreakpoint: 768px;
@xSmallBreakpoint: 480px;